import React, { useState } from 'react';
import './FAQ.css'; // Adicione um arquivo CSS para estilizar o componente

const FAQ = () => {
  const [expanded, setExpanded] = useState(null);

  const toggleFAQ = (index) => {
    setExpanded(expanded === index ? null : index);
  };

  const faqData = [
    {
      question: 'O que √© o SustainaAI?',
      answer: 'O SustainaAI √© uma plataforma que utiliza intelig√™ncia artificial para transformar dados em insights valiosos.'
    },
    {
      question: 'Como posso come√ßar a usar?',
      answer: 'Voc√™ pode come√ßar criando uma conta e importando seus dados para come√ßar a an√°lise.'
    },
    {
      question: 'Quais s√£o os principais recursos?',
      answer: 'Oferecemos machine learning, an√°lise preditiva e automa√ß√£o para otimizar suas opera√ß√µes.'
    }
  ];

  return (
    <section className="FAQ">
      <h2>Perguntas Frequentes</h2>
      <div className="FAQ-list">
        {faqData.map((item, index) => (
          <div key={index} className="FAQ-item">
            <button className="FAQ-question" onClick={() => toggleFAQ(index)}>
              {item.question}
            </button>
            {expanded === index && (
              <div className="FAQ-answer">
                <p>{item.answer}</p>
              </div>
            )}
          </div>
        ))}
      </div>
    </section>
  );
};

export default FAQ;
